<template>
  <div>
    <h3>百度地图</h3>
    <div id="allmap" style="height:400px"></div>
    <p>返回北京市“景点”关键字的检索结果，并展示在地图上</p>
  </div>
</template>
<script>
console.log(BMap);
export default {
  mounted() {
    //   console.log(1);
    // 百度地图API功能
    var map = new BMap.Map("allmap");
    var point = new BMap.Point(121.48,31.41);
    map.centerAndZoom(point, 15);
    var marker = new BMap.Marker(point); // 创建标注
    map.addOverlay(marker); // 将标注添加到地图中
    marker.setAnimation(BMAP_ANIMATION_BOUNCE);
    //跳动的动画
    var local = new BMap.LocalSearch(map, {
      renderOptions: { map: map }
    });
    local.search("宝山");
    
  }
};
</script>
<style scoped>
body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: "微软雅黑";
}
#allmap {
  width: 100%;
  height: 500px;
}
p {
  margin-left: 5px;
  font-size: 14px;
}
</style>

