    <input type="button" value="登 录" class="login-btn"  @click="login"/>
    
  </div>-->
  <van-cell-group style="text-align:center">请登录
    <van-field
      v-model="username"
      required
      clearable
      label="用户名"
      right-icon="question-o"
      placeholder="请输入用户名"
      @click-right-icon="$toast('question')"
     
    />

    <van-field v-model="password" type="password" label="密码" placeholder="请输入密码" required   style="margin-bottom:30px"/>
    
    <router-link to="/login" class="a"><van-button type="primary" size="large" @click="login" style="margin-bottom:0.2px">登录</van-button></router-link>
    <router-link to="/reg" class="a"><van-button type="primary" size="large">注册</van-button></router-link>
   
  
  </van-cell-group>
  
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      username: "",
      password: "",
      errmsg: "",
      value: ""
    };
  },
  methods: {
    login() {
      axios({
        url: "/api/login",
        method: "post",
        data: {
          username: this.username,
          password: this.password
        }
      }).then(res =>
        res.data.err === 0
          ? this.$router.push("/user")
          : (this.errmsg = res.data.msg)
      );
    }
  }
};
</script>
